'use client';

import { motion } from 'framer-motion';
import { Phone, University, CalendarClock, Globe } from 'lucide-react';

const contactData = [
  {
    icon: Phone,
    title: 'الاستعلامات',
    value: '963996171681',
    color: 'text-blue-500',
    bg: 'bg-blue-100',
  },
  {
    icon: University,
    title: 'التخصصات',
    value: '92004531212',
    color: 'text-green-600',
    bg: 'bg-green-100',
  },
  {
    icon: CalendarClock,
    title: 'الفروع',
    value: '92004531212',
    color: 'text-purple-600',
    bg: 'bg-purple-100',
  },
  {
    icon: Globe,
    title: 'ساعات العمل',
    value: 'تصفح الموقع',
    color: 'text-orange-600',
    bg: 'bg-orange-100',
  },
];

export default function ContactPage() {
  return (
    <section className="min-h-screen bg-gradient-to-br from-blue-50 via-purple-50 to-white py-12 px-4 md:px-10" dir="rtl">
      <div className="max-w-6xl mx-auto text-center">
        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          className="text-3xl md:text-4xl font-bold text-purple-800 mb-10"
        >
          تواصل معنا
        </motion.h1>

        <motion.div
          initial="hidden"
          animate="visible"
          variants={{
            visible: { transition: { staggerChildren: 0.2 } },
          }}
          className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 lg:grid-cols-4 gap-6"
        >
          {contactData.map(({ icon: Icon, title, value, color, bg }, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, y: 30 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.4, delay: index * 0.1 }}
              className={`rounded-xl shadow-lg p-6 bg-white hover:shadow-purple-300 border-t-4 ${bg} transition`}
            >
              <div className="flex items-center justify-end gap-4">
                <div className={`p-3 rounded-full ${bg}`}>
                  <Icon size={28} className={color} />
                </div>
                <div className="text-right">
                  <h3 className="text-lg font-semibold text-gray-800">{title}</h3>
                  <p className="text-sm text-gray-600 mt-1">{value}</p>
                </div>
              </div>
            </motion.div>
          ))}
        </motion.div>
      </div>
    </section>
  );
}